import * as React from 'react';
import './portal.css';
import AppLayout from './layout';
import MyApplication from './components/MyApplication';
import { ScheduleProvider } from './context/scheduleContext';
import { ResultProvider } from './context/resultContext';
import { ModalScreeningProvider } from './context/preScreeningQuestion';

function App() {
  return (
    <ScheduleProvider>
      <ResultProvider>
        <ModalScreeningProvider>
          <AppLayout>
            <MyApplication />
          </AppLayout>
        </ModalScreeningProvider>
      </ResultProvider>
    </ScheduleProvider>
  );
}

export default App;
